.work_together_container {
    background-color: rgb(15, 15, 15);

    color: #4AD592;
    height: 632px;
    display: flex;
    justify-content: space-around;
    max-width: 1920px;

}
.skillset_section{
    max-width: 1920px;
}

.letz_work_together {
    font-size: 121px;
    font-weight: bold;
    line-height: 121px;
    text-align: start;
    align-items: center;
    display: flex;
    font-family: 'Baloo 2';
}

.d_none_responsiv_ball{
    height:90px;
    width: 90px;
    display: none;
}

.sectionball {
    position: absolute;
    top: 820px;
    left: 600px;
    height: 300px;
    z-index: 99;
}


.work_together_img_text_buttoncontainer {
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: column;

    img {
        height: 100px;
        width: 100px;
    }

    span {
        font-size: 18px;
        font-family: 'Montserrat';
        font-weight: bold;
    }
}

.skill_set_continer {
    height: 632px;
    background-color: #4AD592;
    span {
        font-size: 96px;
        font-weight: bold;
        font-family: 'Baloo 2';
        display: flex;
        justify-content: center;
        padding-top: 20px;
    }
}

.skill_set_img_container {
 display: flex;
 flex-wrap: wrap;
 align-items: center;

    img {
        width: 120px;
        margin-left: 186px;
       
        transition: transform 0.7s;

        &:hover {
            transform: scale(1.5, 1.5);
            -webkit-filter: invert(1);
            filter: invert(1);
        }
    }
}

.skill_img {
    display: flex;
    justify-content: space-between;
    gap: 44px;
    margin-top: 24px;
}

.skill_img_bot {
    gap: 44px;
    margin-top: 100px;
    display: flex;
    justify-content: space-between;
}

.send_a_message_button {
    color: #4AD592;
    border: 2px solid #4AD592;
    border-radius: 7px;
    height: 58px;
    width: 254px;
    background-color: #0F0F0F;

    transition: background-color 1.1s;

    text-decoration: none;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    overflow: hidden;
    background: linear-gradient(to top left, transparent 50%, #4AD592 50%);
    background-size: 200% 200%;
    background-position: bottom right;
    color: #4AD592;
    transition: all 0.25s ease;
    font-family: 'Baloo 2';
    &:hover {
        background-position: top left;
        color: rgb(19, 19, 19);
        background-color: #4AD592;
        color:whitesmoke;
        text-shadow: 1px 1px 4px black;
        transform: scale(1.1);
       font-size: 20px;
       font-weight: bold;
       font-family: 'Baloo 2';
       
    }
}

  
